import React from "react";
import Signup from "../components/Signup";
import logo from "../res/roommates40plusIcon.jpg";
import twoWomen from "../res/Two-women-coffee.jpg";

const Home = props => {
    return (
        <div className="container-fluid" style={{ marginTop: "52px" }}>
            <div className="container">
                <div className="row">
                    <div className="col-md-12">
                        Ad Space
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-8">
                        <div style={{ textAlign: "center", margin: "4px 0", backgroundColor: "#527591", color: "white" }}>
                            <h4 style={{ padding: "6px" }}>Welcome</h4>
                        </div>
                        <img src={logo} width="100%" alt="Roommates 40+ logo" />
                    </div>
                    <div className="col-md-4">
                        <div style={{ textAlign: "center", margin: "4px 0", backgroundColor: "#527591", color: "white" }}>
                            <h4 style={{ padding: "6px" }}>Create a new account</h4>
                        </div>
                        <Signup />
                    </div>
                </div>
                <div className="row">
                    <div style={{ backgroundColor: "#527591", marginTop: "40px", padding: "8px", textAlign: "center", color: "white" }}>
                        <h4>About Us</h4>
                    </div>
                </div>
                <div className="row" style={{ margin: "8px 0" }}>
                    <div className="col-md-5">
                        <img src={twoWomen} width="100%" alt="Roommates 40+ logo" />
                    </div>
                    <div className="col-md-7">
                        <p>
                            Bee!
Don't freak out! My entire species...
What are you doing?
Wait a minute! I'm an attorney!
Who's an attorney?
Don't move.
Oh, Barry.
Good afternoon, passengers. This is your captain. Would a Miss Vanessa Bloome in 24B please report to the cockpit? And please hurry!
What happened here?
There was a DustBuster, a toupee, a life raft exploded.
One's bald, one's in a boat, they're both unconscious!
Is that another bee joke?
No!
No one's flying the plane!
This is JFK control tower, Flight 356. What's your status?
This is Vanessa Bloome. I'm a florist from New York.
Where's the pilot?
He's unconscious, and so is the copilot.
Not good. Does anyone onboard have flight experience?
As a matter of fact, there is.
Who's that?
Barry Benson.
From the honey trial?! Oh, great.
Vanessa, this is nothing more than a big metal bee.
It's got giant wings, huge engines.
I can't fly a plane.
Why not? Isn't John Travolta a pilot?
Yes.
How hard could it be?
Wait, Barry!
We're headed into some lightning.
This is Bob Bumble. We have some late-breaking news from JFK Airport, where a suspenseful scene is developing.
Barry Benson, fresh from his legal victory...
That's Barry!
...is attempting to land a plane, loaded with people, flowers and an incapacitated flight crew.
Flowers?!
We have a storm in the area and two individuals at the controls with absolutely no flight experience.
Just a minute. There's a bee on that plane.
I'm quite familiar with Mr. Benson and his no-account compadres.
They've done enough damage.
But isn't he your only hope?
Technically, a bee shouldn't be able to fly at all.
Their wings are too small... Haven't we heard this a million times?
"The surface area of the wings and body mass make no sense."
                        </p>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Home;


