import React from "react";

const NavBar = props => {
    return (
        <div>
            <nav style={{backgroundColor:"#527591", borderColor:"#3D5B97"}} className="navbar navbar-default navbar-fixed-top">
                <div className="container">
                    <div className="navbar-header">
                        <button style={{backgroundColor:"#527591", borderColor:"#3D5B97"}} type="button" className="navbar-toggle" data-toggle="collapse" data-target="#myNavbar">
                            <span style={{backgroundColor:"white"}} className="icon-bar"></span>
                            <span style={{backgroundColor:"white"}} className="icon-bar"></span>
                            <span style={{backgroundColor:"white"}} className="icon-bar"></span>
                        </button>
                        <a style={{color:"white"}} className="navbar-brand" href="#">Roommates 40+</a>
                    </div>
                    <div style={{borderColor:"#3D5B97"}} className="collapse navbar-collapse" id="myNavbar">
                        <form className="navbar-form navbar-right" style={{borderColor:"#3D5B97"}} action="/action_page.php">
                            <div className="form-group">
                                <input style={{margin:"1px"}} type="text" className="form-control input-sm" placeholder="Email" />
                                <input style={{margin:"1px"}} type="password" autoComplete="new-password" className="form-control input-sm" placeholder="Password" />
                            </div>
                            <button style={{margin:"1px", color:"#3D5B97"}} type="submit" className="btn btn-default btn-sm">Login</button>
                        </form>
                    </div>
                </div>
            </nav>
            {props.children}
        </div>
    )
}

export default NavBar;


