import React from "react";

class Signup extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            firstname: "",
            lastname: "",
            email: "",
            password: "",
            birthdate: "",
            gender: "",
            terms: false
        }
        this.sendRequest = this.sendRequest.bind(this);
    }

    sendRequest(event) {
        event.preventDefault();
        console.log(this.props);
    }

    render() {
        return (
            <form style={{ padding: "10px 0" }}>
                <div className="form-group">
                    <input type="text" className="form-control" value={this.state.firstname} onChange={e => this.setState({firstname: e.target.value})} placeholder="First name" />
                </div>
                <div className="form-group">
                    <input type="text" className="form-control" value={this.state.lastname} onChange={e => this.setState({lastname: e.target.value})} placeholder="Last name" />
                </div>
                <div className="form-group">
                    <input type="email" className="form-control" value={this.state.email} onChange={e => this.setState({email: e.target.value})} placeholder="Email" />
                </div>
                <div className="form-group">
                    <input type="password" autoComplete="new-password" className="form-control" value={this.state.password} onChange={e => this.setState({password: e.target.value})} placeholder="Password" />
                </div>
                <div className="form-group">
                    <label>Birthday:</label>
                    <input type="text" className="form-control" value={this.state.birthdate} onChange={e => this.setState({birthdate: e.target.value})} placeholder="YYYY-MM-DD" />
                </div>
                <div className="form-group ">
                    <label className="radio-inline"><input type="radio" name="optradio" onClick={e => this.setState({gender: e.target.value})} value="male" />Male</label>
                    <label className="radio-inline"><input type="radio" name="optradio" onClick={e => this.setState({gender: e.target.value})} value="female" />Female</label>
                </div>
                <div className="checkbox">
                    <label><input type="checkbox" value={this.state.terms} onClick={e => this.setState({terms: e.target.checked})} /> I agree to the Terms and Conditions</label>
                </div>
                <button type="submit" onClick={this.sendRequest} className="btn btn-default">Sign Up</button>
            </form>
        )
    }
}

export default Signup;


